/* eslint-disable no-bitwise */
const BASE = 0xac00;

const INITIALS = ',,,,,,,,,,,,,,,,,,'.split(',');
const MEDIALS = ',,,,,,,,,,,,,,,,,,,,'.split(',');
const FINALES = ',,,,,,,,,,,,,,,,,,,,,,,,,,,'.split(',');

const STROKES: any = [
  ['', 0],
  ['', 2],
  ['', 4],
  ['', 2],
  ['', 3],
  ['', 6],
  ['', 5],
  ['', 4],
  ['', 4],
  ['', 8],
  ['', 2],
  ['', 4],
  ['', 1],
  ['', 3],
  ['', 6],
  ['', 4],
  ['', 3],
  ['', 4],
  ['', 4],
  ['', 3],
  ['', 4],
  ['', 5],
  ['', 5],
  ['', 7],
  ['', 9],
  ['', 9],
  ['', 7],
  ['', 9],
  ['', 9],
  ['', 8],
  ['', 6],
  ['', 2],
  ['', 3],
  ['', 3],
  ['', 4],
  ['', 2],
  ['', 3],
  ['', 3],
  ['', 4],
  ['', 2],
  ['', 4],
  ['', 5],
  ['', 3],
  ['', 3],
  ['', 2],
  ['', 4],
  ['', 5],
  ['', 3],
  ['', 3],
  ['', 1],
  ['', 2],
  ['', 1],
].reduce((accum, [key, count]) => ({ ...accum, [key]: count }), {});

const getSymbol = (char: string): any => {
  if (!char.match(/[--]/)) {
    return false;
  }
  let initial = '';
  let medial = '';
  let finale = '';
  if (char.match(/[-]/)) {
    initial = char;
  } else {
    const tmp = char.charCodeAt(0) - BASE;
    const finaleOffset = tmp % FINALES.length;
    const medialOffset = ((tmp - finaleOffset) / FINALES.length) % MEDIALS.length;
    const initialOffset = ((tmp - finaleOffset) / FINALES.length - medialOffset) / MEDIALS.length;
    initial = INITIALS[initialOffset];
    medial = MEDIALS[medialOffset];
    finale = FINALES[finaleOffset];
  }
  const initialStrokes = STROKES[initial];
  const medialStrokes = STROKES[medial];
  const finaleStrokes = STROKES[finale];
  return {
    initial,
    medial,
    finale,
    initialStrokes,
    medialStrokes,
    finaleStrokes,
    numOfStrokes: initialStrokes + medialStrokes + finaleStrokes,
  };
};

function getScore(name1: any, name2: any) {
  const symbols1 = name1.match(/[\s\S]/g).map((char: string) => [char, getSymbol(char).numOfStrokes]);
  const symbols2 = name2.match(/[\s\S]/g).map((char: string) => [char, getSymbol(char).numOfStrokes]);
  const maxLen = Math.max(symbols1.length, symbols2.length);

  const [chars, numbers] = Array(maxLen)
    .fill(true)
    .reduce(
      ([accum1, accum2], e, i) => {
        const [char1, num1] = symbols1[i] || ['', 0];
        const [char2, num2] = symbols2[i] || ['', 0];
        return [
          [...accum1, char1, char2],
          [...accum2, num1, num2],
        ];
      },
      [[], []],
    );

  let nums = numbers.slice();
  const stages = [nums];
  while (nums.length > 2 && nums.join('') !== '100') {
    nums = nums.reduce((a: any, e: any, i: number, arr: any[]) => {
      if (i < arr.length - 1) {
        return [...a, (e + arr[i + 1]) % 10];
      }
      return a;
    }, []);
    stages.push(nums);
  }

  return { score: ~~stages.slice(-1)[0].join(''), chars, stages };
}

function shuffle(arr: any[]) {
  const array = arr.slice();
  let counter = array.length;
  // While there are elements in the array
  while (counter > 0) {
    // Pick a random index
    const index = Math.floor(Math.random() * counter);
    // Decrease counter by 1
    counter -= 1;
    // And swap the last element with it
    const temp = array[counter];
    array[counter] = array[index];
    array[index] = temp;
  }
  return array;
}

export { getSymbol, getScore, shuffle };
