import React, { useMemo, useState, useRef, SyntheticEvent } from 'react';
import TextareaAutosize from 'react-textarea-autosize';
import styles from './Converter.module.scss';

const sample =
  '   ,          ,         .\n\n              .\n\n,             .';

function jumble(word: string) {
  const letters: any = word.match(/[\s\S]/g);
  const [i1, i2] = letters
    .map((e: any, i: number) => i)
    .slice(1, -1)
    .map((e: any) => [Math.random(), e])
    .sort((a: any, b: any) => a[0] - b[0])
    .map((e: any) => e[1]);
  if (i1 && i2) {
    const tmp = letters[i1];
    letters[i1] = `<span class="${styles.i1}">${letters[i2]}</span>`;
    letters[i2] = `<span class="${styles.i2}">${tmp}</span>`;
  }
  return letters.join('');
}

function Converter() {
  const [text, setText] = useState(sample);

  const handleChange = (event: SyntheticEvent) => {
    // @ts-ignore
    setText(event.target.value.trim());
  };

  const cache = useRef<any>({});
  const converted = useMemo(
    () => ({
      __html: text
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/[-]+/g, (word) => {
          if (!cache.current[word]) {
            cache.current[word] = jumble(word);
          }
          return cache.current[word];
        }),
    }),
    [text],
  );

  return (
    <div className={styles.root}>
      <div className={styles.input}>
        <TextareaAutosize defaultValue={sample} onChange={handleChange} />
      </div>
      <div className={styles.output} dangerouslySetInnerHTML={converted} />
    </div>
  );
}

export default Converter;
